import { Component, Vue } from "vue-property-decorator";

import { STHeader,STFooter } from "@/layout";
import { VideoModel } from "@/models";
import { StCommon } from "../Common";

type response = { [key: string]: unknown };

@Component<ApStudentVideosIndexController>({
    components: {
        STHeader,STFooter
    }
})
export default class ApStudentVideosIndexController extends Vue {
    private videoList: object = {};
    private num = 0;
    private page = 1;
    private perpage = 50;
    private maxPage = 0;
    private token = window.localStorage.getItem('studentToken') as string;

    public created() {
        this.getVideoList();
    }

    private async getVideoList() {
        const item = {
            page: this.page,
            perpage: this.perpage,
            token: this.token,
        }
        const data = await VideoModel.stGetList(item) as response;
        StCommon.checkToken(data.ERR_CODE as number);
        this.num = data.num as number;
        this.videoList = data.list as object;
        this.maxPage = Math.ceil(this.num/this.perpage);
    }

    private getPageList(page: number) {
        this.page = (page < 1) ? 1 : (page > this.maxPage) ?  this.maxPage : page;
        this.getVideoList();
    }
}